import React from 'react';
import '../../../node_modules/font-awesome/css/font-awesome.min.css';

const WeatherItem = ({ weather }) => {
    let weatherIcon = '';
    const {
        current_observation: {
            astronomy: {
                sunrise,
                sunset
            },
            condition: {
                temperature,
                text: weatherText
            },
            atmosphere: {
                humidity
            },
            wind: {
                speed: windSpeed
            }
        },
        location: {
            city,
            country
        }
    } = weather;

    switch (true) {
        case weatherText === 'Mostly Cloudy':
        case weatherText === 'Partly Cloudy':
        case weatherText === 'Cloudy':
            weatherIcon = 'fa-cloud';
            break;
        case weatherText === 'Snow':
        case weatherText === 'Snow Showers':
            weatherIcon = 'fa-snowflake-o';
            break;
        case weatherText === 'Rain':
        case weatherText === 'Showers':
        case weatherText === 'Scattered Showers':
            weatherIcon = 'fa-tint';
            break;
        default:
            weatherIcon = 'fa-sun-o';
    }

    const weatherArray = [
        {
            name: sunrise,
            name2: sunset,
            icon: 'fa-sun-o',
            icon2: 'fa-moon-o',
            prefix: null,
            prefix2: null
        },
        {
            name: temperature,
            name2: humidity,
            icon: 'fa-thermometer-full',
            icon2: 'fa-tint',
            prefix: 'o',
            prefix2: '%'
        },
        {
            name: weatherText,
            name2: windSpeed,
            icon: weatherIcon,
            icon2: 'fa-align-justify',
            prefix: null,
            prefix2: 'm/s'
        },
    ];
    
    return (
        <>
            <h4>{`${city}, ${country}`}</h4>
            <ul className="weather-list">
                {
                    weatherArray.map((
                        { name, name2, 
                        icon, icon2, 
                        prefix, prefix2
                        }) => {
                        return (
                            <li key={name}>
                                <div className="weather-list__container">
                                    <div className="weather-list__item">
                                        <i className={`fa ${icon}`} />
                                        <span>
                                            {name}
                                            <sup>{prefix}</sup>
                                        </span>
                                    </div>
                                    <div className="weather-list__item">
                                        <i className={`fa ${icon2}`} />
                                        <span>
                                            {name2}
                                            <sup>{prefix2}</sup>
                                        </span>
                                    </div>
                                </div>
                            </li>
                        )
                    })  
                }
            </ul>
            <ul className="weather-list-week">
                {
                    weather.forecasts.slice(0, 5).map(({ day, low, high, text }) => {
                        let weatherWeekIcon = '';
                        switch (true) {
                            case text === 'Mostly Cloudy':
                            case text === 'Partly Cloudy':
                            case text === 'Cloudy':
                                weatherWeekIcon = 'fa-cloud';
                                break;
                            case text === 'Snow':
                            case text === 'Snow Showers':
                                weatherWeekIcon = 'fa-snowflake-o';
                                break;
                            case text === 'Rain':
                            case text === 'Showers':
                            case text === 'Scattered Showers':
                                weatherWeekIcon = 'fa-tint';
                                break;
                            default:
                                weatherWeekIcon = 'fa-sun-o';
                        }

                        return (
                            <li key={day}>
                                <div><i className={`fa ${weatherWeekIcon}`} /></div>
                                <div>{day}</div>
                                <div className="temperature">
                                    {low}
                                    <sup>o</sup>C
                                </div>
                                <div className="temperature">
                                    {high}
                                    <sup>o</sup>C
                                </div>
                            </li>
                        )
                    })
                }
            </ul>
        </>
    )
};

export default WeatherItem;
