import React, { useState } from 'react';
import { Form, Button, Modal } from 'react-bootstrap';

const LoginModal= ({show, onHide, Login, error}) => {
    const [details, setDetails] = useState({name: '', email: '', password: ''})
    const handleSubmit = (e) => {
        e.preventDefault();

        Login(details);
        setDetails({name: '', email: '', password: ''});
    };

    return (
        <Modal show={show} onHide={onHide}>
            <Modal.Header closeButton>
                <Modal.Title>Login</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <form onSubmit={handleSubmit} noValidate>
                    {error !== '' ? <div className="modal-error mb-3">{error}</div> : '' }
                    <Form.Group controlId="modalName" className="mb-3">
                        <Form.Label>Name:</Form.Label>
                        <Form.Control 
                            type="text"
                            name="modalName"
                            placeholder="Name"
                            onChange={e => setDetails({...details, name: e.target.value})}
                            value={details.name}
                            className={'input'}
                        />
                    </Form.Group>
                    <Form.Group controlId="modalEmail" className="mb-3">
                        <Form.Label>Email:</Form.Label>
                        <Form.Control 
                            type="email"
                            name="modalEmail"
                            placeholder="Email"
                            onChange={e => setDetails({...details, email: e.target.value})}
                            value={details.email}
                            className={'input'}
                            required
                        />
                    </Form.Group>
                    <Form.Group controlId="modalPassword" className="mb-4">
                        <Form.Label>Password:</Form.Label>
                        <Form.Control
                            type="password"
                            name="modalPassword"
                            placeholder="*****"
                            onChange={e => setDetails({...details, password: e.target.value})}
                            value={details.password}
                            className={'input'}
                            required
                        />
                    </Form.Group>
                    <Modal.Footer style={{justifyContent: 'center'}}>
                        <Button variant="primary" type="submit">Login</Button>
                    </Modal.Footer>
                </form>
            </Modal.Body>
        </Modal>
    );
};

export default LoginModal;