import React, { useContext, useRef} from 'react';
import { Form, Button } from 'react-bootstrap';
import useForm from "../validate/useForm";
import validate from './FormValidationRules';
import { FormContext } from './index';

const HomeForm = () => {
    const {
        values,
        errors,
        handleChange,
        handleSubmit,
    } = useForm(formSuccess, validate);
    const { status, setStatus } = useContext(FormContext);
    const { setModalResult } = useContext(FormContext);
    const { setShow } = useContext(FormContext);
    const handleShow = () => setShow(true);
    const formRef = useRef();

    async function formSuccess() {
        setStatus('Sending...');
  
        let { name, email, message } = formRef.current.elements;
        let details = {
            name: name.value,
            email: email.value,
            message: message.value,
        };
        let response = await fetch('http://localhost:5000/contact', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
            },
            body: JSON.stringify(details),
        })
        .catch(err => {
            console.log(err);
            return;
        });
    
        setStatus(status);
        try {
            setModalResult(await response.json());
        } catch (err) {
            console.log(err);
        }
        values.name = '';
        values.email = '';
        formRef.current.reset();
        handleShow();
    }

    return (
        <>
        <h1 className="mb-3">Contact us form</h1>
        <form onSubmit={handleSubmit} noValidate ref={formRef}>
            <Form.Group controlId="name" className="mb-3">
                <Form.Label>Name:</Form.Label>
                <Form.Control 
                    type="text"
                    name="name"
                    placeholder="Enter your name"
                    className={`input ${errors.email && 'is-danger'}`}
                    onChange={handleChange}
                    value={values.name || ''}
                    autoComplete="off"
                    required
                />
                {errors.name && (
                  <p className="help is-danger">{errors.name}</p>
                )}
            </Form.Group>
            <Form.Group controlId="email" className="mb-3">
                <Form.Label>Email:</Form.Label>
                <Form.Control
                    type="email"
                    name="email"
                    placeholder="Enter your email"
                    onChange={handleChange}
                    className={`input ${errors.email && 'is-danger'}`}
                    value={values.email || ''}
                    autoComplete="off"
                    required
                />
                {errors.email && (
                    <p className="help is-danger">{errors.email}</p>
                )}
            </Form.Group>
            <Form.Group controlId="message" className="mb-3">
                <Form.Label>Message:</Form.Label>
                <Form.Control 
                    as="textarea"
                    name="message"
                    rows="3"
                    placeholder="Enter your message"
                />
            </Form.Group>
            <Button variant="primary" type="submit">{status}</Button>
        </form>
        </>
    )
}

export default HomeForm;
