import React, { useState } from 'react';
import LoginModal from './LoginModal';

const Login = () => {
    const [show, setShow] = useState(false);
    const handleShow = () => setShow(true);
    const handleClose = () => setShow(false);
    const [user, setUser] = useState({email: '', password: ''});
    const [error, setError] = useState('');
    const isLocalStorage = localStorage.getItem('login');
    const userName = localStorage.getItem('name');
    const adminUser = {
        email: 'admin@admin.com',
        password: 'admin'
    };    
    const Login = details => {
        if (details.email === adminUser.email && details.password === adminUser.password) {
            handleClose();
            setUser({
                name: details.name,
                emeil: details.email
            });
            localStorage.setItem('login', 'true');
            localStorage.setItem('name', details.name);
        } else {
            setError('Email or password is incorrect');
        }
    };

    const Logout = () => {
        setUser({email: '', password: ''});
        localStorage.removeItem('login');
        localStorage.removeItem('name');
    }

    return (
        <div className="login">
            <div className="user-name">
                {(user.email !== '' || isLocalStorage ? 
                    <div>Welcome, <b>{userName || 'user'}</b></div>
                    : 
                    null
                )}
            </div>
            {!isLocalStorage
                ?
                <button onClick={handleShow} className="btn btn-primary">Login</button> 
                :
                <button onClick={Logout} className="btn btn-secondary">Logout</button>
            }
            <LoginModal 
                show={show}
                onHide={handleClose}
                Login={Login}
                error={error}
            />
        </div>
    );
};

export default Login;
