import React, { useState, useCallback, useRef, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import Spinner from 'react-bootstrap/Spinner';
import { Button, Form, Pagination } from 'react-bootstrap';
import useSortableData from './useSortableData';
import logo from '../../images/logo.png';

const BTC = () => {
    let PaginationItems = [];
    const [crypto, setCrypto] = useState([]);
    const [isSending, setIsSending] = useState(false);
    const [loading, setLoading] = useState(true);
    const [search, setSearch] = useState('');
    const [currency, setCurrency] = useState('usd');
    const [paginationActivePage, setPaginationActivePage] = useState(1);
    const isMounted = useRef(true);
    const navigate = useNavigate();
    const { items, requestSort, sortConfig } = useSortableData(crypto);
    const getClassNamesFor = (name) => {
        if (!sortConfig) {
            return;
        }
        return sortConfig.key === name ? sortConfig.direction : undefined;
    };

    for (let number = 1; number <= 5; number++) {
        PaginationItems.push(
          <Pagination.Item key={number} active={number === paginationActivePage}>
            {number}
          </Pagination.Item>,
        );
    }

    const filteredCoins = items.filter((item) =>
        item.name.toLowerCase().includes(search.toLowerCase())
    );

    const paginationHandle = (e) => {
        const currentPage = Number(e.target.textContent);

        setPaginationActivePage(currentPage);
    };

    const paginationHandlePrev = () => {
        setPaginationActivePage(paginationActivePage - 1);
    };

    const paginationHandleNext = () => {
        setPaginationActivePage(paginationActivePage + 1);
    };

    const pagination = (
        <Pagination className='pagination-main'>
            <Pagination.Prev disabled={paginationActivePage <= 1} onClick={paginationHandlePrev} />
            <Pagination onClick={paginationHandle}>{PaginationItems}</Pagination>
            <Pagination.Next disabled={paginationActivePage >= 5} onClick={paginationHandleNext} />
        </Pagination>
    );

    useEffect(() => {
        fetch(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&order=market_cap_desc&per_page=100&page=${paginationActivePage}&sparkline=false`)
            .then(res => {
                if (res.ok) return res.json(); 
                else {
                    navigate('/NotFound');
                    return;
                }
            })
            .then(data => {
                setCrypto(data);
                setLoading(false);
            })
            .catch(err => {
                console.log(err);
            });
        return () => {
          isMounted.current = false
        }
    }, [isSending, paginationActivePage, currency]);

    const sendRequest = useCallback(async () => {
        if (isSending) return;
        
        setIsSending(true);
        fetch(`https://api.coingecko.com/api/v3/coins/markets?vs_currency=${currency}&order=market_cap_desc&per_page=100&page=${paginationActivePage}&sparkline=false`)
            .then(res => {
                if (res.ok) return res.json(); 
                else {
                    navigate('/NotFound');
                    return;
                }
            })
            .then(data => {
                setCrypto(data);
                setLoading(false);
                setIsSending(false);
            })
            .catch(err => {
                console.log(err);
            });
        if (isMounted.current) setIsSending(false);
    }, [isSending]);


    const formatPlusMinus = priceChange => {
        const isPositive = Math.sign(priceChange) > 0;
        
        return `${isPositive ? 'positive' : 'negative'}`;
    }

    const toFixedCurrency = curr => {
        return Number(curr).toFixed(2);
    }

    const handleChange = (e) => {
        setSearch(e.target.value);
    };

    const selectChange = (e) => {
        setCurrency(e.target.value);
    };

    const currencyIcon = () => {
        return currency === 'usd' ? '$' : 'â‚¬';
    }

    const getDateAndTime = (date) => {
        let res = '';
        const newDate = new Date(date).toLocaleDateString();
        const hours = new Date(date).getHours();
        const minutes = new Date(date).getHours();
        const seconds = new Date(date).getSeconds();

        res = `${newDate} / ${hours}:${minutes}:${seconds}`;

        return res;
    }

    return (
        <div className="container">
            <h3 className="crypto-title">Cryptocurrency Prices by Market Cap</h3>
            <nav className="crypto-nav">
                <img className="coin-logo" src={logo} />
                <div className="d-flex">
                    <Form className="d-flex">
                        <Form.Group style={{width: '200px', marginRight: '15px'}}>
                            <Form.Control 
                                type="text"
                                onChange={handleChange}
                                placeholder="Search a Coin"
                            />
                        </Form.Group>
                        <Form.Select style={{width: '100px', marginRight: '15px'}} onChange={selectChange}>
                            <option value="usd">USD</option>
                            <option value="eur">EUR</option>
                        </Form.Select>
                    </Form>
                    <Button onClick={sendRequest} disabled={isSending} variant="secondary">Fetch last data</Button>
                </div>
            </nav>
            {loading ?
            <Spinner animation="border" role="status">
                <span className="visually-hidden">Loading...</span>
            </Spinner> :
            <>
                <table className="table">
                    <thead>
                        <tr>
                            <th colSpan="1">
                            <button
                                type="button"
                                onClick={() => requestSort('market_cap_rank')}
                                className={getClassNamesFor('market_cap_rank')}
                            >
                                #
                            </button>
                            </th>
                            <th colSpan="1">
                            <button
                                type="button"
                                onClick={() => requestSort('name')}
                                className={getClassNamesFor('name')}
                            >
                                Name
                            </button>
                            </th>
                            <th colSpan="1">
                            <button
                                type="button"
                                onClick={() => requestSort('current_price')}
                                className={getClassNamesFor('current_price')}
                            >
                                Last Price
                            </button>
                            </th>
                            <th colSpan="1">%</th>
                            <th colSpan="1">24 high</th>
                            <th colSpan="1">24 low</th>
                            <th colSpan="1">Last Updated</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredCoins.length ?
                        filteredCoins.map(item => {
                            return (
                                <tr key={item.id} className={formatPlusMinus(item.market_cap_change_percentage_24h)}>
                                    <td>
                                        <b>{item.market_cap_rank}</b>
                                    </td>
                                    <td>
                                        <img src={item.image} alt="" />
                                        {item.name}
                                    </td>
                                    <td>
                                        {currencyIcon()}
                                        {toFixedCurrency(item.current_price)}
                                    </td>
                                    <td className={'persent'}>
                                        {toFixedCurrency(item.market_cap_change_percentage_24h)}%
                                    </td>
                                    <td>
                                        {currencyIcon()}
                                        {toFixedCurrency(item.high_24h)}
                                    </td>
                                    <td>
                                        {currencyIcon()}
                                        {toFixedCurrency(item.low_24h)}
                                    </td>
                                    <td>
                                        {getDateAndTime(item.last_updated)}
                                    </td>
                                </tr>
                            )
                        })
                        :
                        <tr>
                            <td colSpan="7">
                                Coins not found
                            </td>
                        </tr>
                        }
                    </tbody>
                </table>
                {pagination}
            </>
            }
        </div>
    );
}


export default BTC;